@mixin den($mau: #345,$cl : #fff,$bf :#000) {
    background: $mau;
    min-height: 368px;
    color: $cl;
    font-family: 'Roboto', sans-serif;
    justify-content: center;
    align-items: center;
    display: flex;
    text-align: center;
    padding-top: 5rem;
    padding-bottom: 5rem;
    h2 {
        text-transform: uppercase;
        position: relative;
        &:before {
            content: '';
            width: 5rem;
            height: .15rem;
            background: $bf;
            display: block;
            position: absolute;
            transform: translateX(-50%);
            left: 50%;
            bottom: -20px;
            opacity: 0.4; } } }
@mixin layout($mau1) {
    background-color: $mau1;
    justify-content: center;
    align-items: center;
    text-align: center; }
